* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: sans-serif;
  text-align: center;
  font-family: "Lato";
}

p {
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
}

.title {
  color: black;
  font-size: 50px;
  text-shadow: 2px 3px 1px rgba(0, 0, 0, 0.25);
  margin-top: 100px;
  margin-bottom: 20px;
}

/* HOME */

.home-container {
  position: relative;
  height: 450px;
  background: rgb(29, 26, 26);
  background-image: url("dental.webp");
  background-size: max(100%, 770px) auto;
  background-position: bottom 25% center;
  background-repeat: no-repeat;
  background-blend-mode: screen;
  padding-top: 50px;
  margin-bottom: 50px;
}

.logo {
  /* position: absolute;
  top: -50px;
  left: 50%; */
  /* transform: translateX(-50%); */
  margin: 0 auto;
  font-size: 100px;
  text-align: center;

  color: #ffffff;

  text-shadow: 8px 6px 4px rgba(0, 0, 0, 0.25);
}

.logo + p {
  color: white;
  font-size: 40px;
}

.navigation {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

.navigation a {
  position: relative;

  font-family: Bree Serif;
  font-size: 30px;
  line-height: 100px;
  text-align: center;
  text-decoration: none;
  padding: 30px;
  white-space: nowrap;

  color: #ffffff;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.nav-background {
  height: 100px;
  width: 80%;

  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  background-color: rgba(0, 0, 0, 0.25);
  filter: blur(5px);
}

.burger {
  position: fixed;
  /* width: 100%; */
  z-index: 2;
  padding: 2rem;
  text-align: center;
  background-color: none;
  transition: 0.2s;
  /* visibility: hidden; */
  margin: 5px 0 0 5px;
  cursor: pointer;
}

.burger-hidden {
  display: none;
}

.scrolled {
  transition: 0.2s;
  display: block;
}

.line {
  width: 30px;
  height: 5px;
  margin-top: 7px;
  background-color: rgb(134, 142, 142);
  /* background-color: #709fb0; */
  pointer-events: none;
}

nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 10vh;
  /* background: #709fb0; */
}

.nav-links {
  z-index: 1;
  position: fixed;
  display: flex;
  text-decoration: none;
  list-style: none;
  flex-direction: column;
  /* *******************************************************8 */
  width: 300px;
  height: 300px;
  background: rgb(211, 213, 213);
  box-shadow: 6px 9px 8px rgba(0, 0, 0, 0.35);
  /* ***********************************************************888 */
  justify-content: space-around;
  align-items: center;
  /* margin-left: auto; */
  clip-path: circle(100px at -90% -10%);
  -webkit-clip-path: circle(100px at -90% -10%);
  transition: all 1s ease-out;
  pointer-events: none;
}

.nav-links.open {
  pointer-events: all;
  clip-path: circle(700px at 90% -10%);
  -webkit-clip-path: circle(700px at 90% -10%);
}

.nav-links li {
  opacity: 0;
}

li.fade {
  opacity: 1;
}

.nav-links li a {
  color: white;
  font-size: 20px;
  text-decoration: none;
}

.white {
  background-color: white;
  opacity: 1;
}

/* ABOUT */
.about-container {
  position: relative;
}

.doc-props {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.doc-img img {
  width: 350px;
  /* height: 350px; */
  box-shadow: 6px 7px 8px rgba(0, 0, 0, 0.25);
}

.doc-props:nth-child(even) {
  flex-direction: row-reverse;
}

.present-box {
  width: 50%;
  text-align: left;
}

.doc-name {
  color: rgb(134, 142, 142);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.doc-props .doc-about,
.doc-props .doc-name {
  position: relative;
  display: inline-block;
  transition: 0.5s;
  transform-origin: left;
  transform: scaleX(1);
  /* opacity: 0; */
}

.doc-props .doc-about:before,
.doc-props .doc-name:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  transform-origin: right;
  transition: 1.1s;
}

.doc-props .doc-img img {
  opacity: 0;
  transition: 1s;
}

.doc-props .doc-img img[data-scroll="in"] {
  opacity: 1;
}

.doc-props .doc-about[data-scroll="in"],
.doc-props .doc-name[data-scroll="in"] {
  transform: scaleX(1);
  opacity: 1;
  transition-delay: 1s;
}
.doc-props .doc-about[data-scroll="in"]:before,
.doc-props .doc-name[data-scroll="in"]:before {
  transform: scaleX(0);
  transition-delay: 0.5s;
}

@media only screen and (max-width: 900px) {
  .cards {
    flex-direction: column;
  }

  .front,
  .back {
    position: relative;
  }
}

@media only screen and (max-width: 810px) {
  .home-container {
    box-sizing: border-box;
    padding-top: 120px;
  }

  .logo {
    margin: 0 auto 15px;
    font-size: 50px;
  }

  .logo + p {
    font-size: 20px;
  }

  .nav-background {
    width: 100%;
    height: 50px;
  }

  .navigation a {
    font-size: 25px;
    line-height: 50px;
    padding: 25px;
  }

  .doc-props {
    flex-direction: column;
  }

  .doc-props:nth-child(even) {
    flex-direction: column;
  }

  .doc-props + .doc-props {
    margin-top: 100px;
  }

  .present-box {
    width: 90%;
    text-align: left;
  }
}

@media only screen and (max-width: 600px) {
  .home-container {
    height: 350px;
  }

  div.list-of-price {
    width: auto;
    margin: 0;
  }

  .navigation a {
    font-size: 20px;
    padding: 10px;
  }

  .contact-container {
    flex-direction: column;
  }

  .icon {
    width: 30px;
    padding: 10px;
    position: center;
  }
}

/* OFFER */

.block-offer {
  clip-path: polygon(5% 1%, 100% 0%, 95% 100%, 0% 100%);
  width: 80%;
  background-color: rgb(211, 213, 213);
  filter: drop-shadow(7px 8px 8px rgba(0, 0, 0, 0.25));
  /* margin-bottom: 50px; */
  margin: auto;
  margin-top: 300px;
}

.offer-title {
  padding: 30px;

  font-size: 50px;
  color: #ffffff;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.cards {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.card-props {
  margin: 50px;
}

/* .wrapper {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minimax(350px, 1fr));
} */

.wrapper .box-offer {
  width: 300px;
  height: 200px;

  position: relative;
  perspective: 1000px;
}

.front {
  cursor: pointer;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;

  background: #fdfdfd;
  border: 8px solid rgb(134, 142, 142);
}

.box-offer .front {
  font-size: 25px;
  transition: all 0.5s ease;
}

.box-offer .back {
  border: 8px solid rgb(134, 142, 142);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: rgb(134, 142, 142);
  opacity: 0;
  transform: translate(-110px) rotateY(-90deg);
  transition: all 0.4s ease;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

.box-offer .back p {
  text-shadow: none;
}

.box-offer:hover .back {
  opacity: 1;
  transform: rotate(0deg);
}

.box-offer:hover .front {
  opacity: 0;
  transform: translate(110px) rotateY(90deg);
}

.dent-icon {
  height: 80px;
}

.offer-card-name {
  color: black;
  font-size: 25px;
}

/* PRICING */

.list-of-price {
  margin: auto;

  box-sizing: border-box;
  width: 50%;
  /* height: 50px; */

  display: flex;
  justify-content: center;

  border-left: rgb(211, 213, 213) solid 30px;
  border-right: rgb(211, 213, 213) solid 30px;
}

.first-column,
.second-column {
  width: 40%;
  text-align: right;
  line-height: 2rem;
  border-bottom: 1px solid rgb(211, 213, 213);
}

.first-column {
  text-align: left;
}

hr {
  width: 60%;
  border: none;
  border-bottom: 1px solid rgb(211, 213, 213);
}

/* CONTACT */

.contact-container {
  position: relative;
  display: flex;
  justify-content: space-around;
}

.box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.icon {
  width: 50px;
  padding: 10px;
  float: left;
}

.map {
  border: none;
  /* allowfullscreen: ""; */
}

/* FOOTER */

.footer {
  background-color: rgb(134, 142, 142);
  color: #fdfdfd;
}

.left-footer{
  width: 50%;
  float: left;
}
